import React, { useState, useEffect, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import {
    Button,
    CircularProgress,
} from "@material-ui/core";
import {
    PlayArrow,
    Stop,
    Favorite,
    Close,
    Videocam,
} from "@material-ui/icons";
import { Select, CaretIcon, ModalCloseButton } from "react-responsive-select";
import { Switch, FormControlLabel } from "@material-ui/core";
import toast from "react-hot-toast";
import {
    getDeviceUrl,
    getSlideshowFavorites,
    removeSlideshowFavorite,
    getSlideshowStatus,
    startSlideshow,
    stopSlideshow,
    updateMarquee,
} from "../../services";
import { readSettings, updateSetting } from "../../services/settings";
import { dispatchTickerStarted, onTickerStarted } from "../../services/tickerEvents";
import "./styles.css";

type FavoriteItem = {
    key: string;      // system-game format
    system: string;
    game: string;
    hasVideo?: boolean;
};

// Interval options
const intervalOptions = [
    { text: "1 second", value: "1" },
    { text: "3 seconds", value: "3" },
    { text: "5 seconds", value: "5" },
    { text: "10 seconds", value: "10" },
    { text: "20 seconds", value: "20" },
    { text: "30 seconds", value: "30" },
    { text: "1 minute", value: "60" },
    { text: "5 minutes", value: "300" },
];

const Favorites: React.FC = () => {
    const nav = useNavigate();
    const [favorites, setFavorites] = useState<FavoriteItem[]>([]);
    const [loading, setLoading] = useState(true);
    const [isSlideshowRunning, setIsSlideshowRunning] = useState(false);
    const [isStarting, setIsStarting] = useState(false);
    const [currentMarquee, setCurrentMarquee] = useState<string>("");
    const [settingsLoaded, setSettingsLoaded] = useState(false);
    const [slideshowSettings, setSlideshowSettings] = useState({
        interval: 3,
        includeVideos: false,
    });

    // Parse a favorite key into system and game
    const parseFavoriteKey = (key: string): FavoriteItem => {
        const firstDash = key.indexOf('-');
        if (firstDash === -1) {
            return { key, system: key, game: key };
        }
        return {
            key,
            system: key.substring(0, firstDash),
            game: key.substring(firstDash + 1),
        };
    };

    // Load favorites and slideshow status
    const loadData = useCallback(async () => {
        try {
            const [favs, status, settings, videoListResponse] = await Promise.all([
                getSlideshowFavorites(),
                getSlideshowStatus(),
                readSettings(),
                fetch(`${getDeviceUrl("")}/list/video`).then(r => r.json()).catch(() => ({ files: [] })),
            ]);

            // Build a set of video basenames (lowercase, without extension)
            const videoSet = new Set<string>();
            if (videoListResponse.files) {
                videoListResponse.files.forEach((filename: string) => {
                    // Remove .mp4 extension and lowercase
                    const basename = filename.replace(/\.mp4$/i, "").toLowerCase();
                    videoSet.add(basename);
                });
            }

            // Parse favorites and check for video availability
            const parsedFavorites = favs.map((key: string) => {
                const item = parseFavoriteKey(key);
                // Check if video exists: try system-game first, then just game
                item.hasVideo = videoSet.has(item.key.toLowerCase()) || videoSet.has(item.game.toLowerCase());
                return item;
            });

            setFavorites(parsedFavorites);
            setIsSlideshowRunning(status.running || false);
            setCurrentMarquee(status.currentMarquee || "");

            if (settings) {
                setSlideshowSettings({
                    interval: settings.slideshowInterval || 3,
                    // Handle both boolean (from API) and string (legacy) values
                    includeVideos: settings.slideshowIncludeVideos === true || settings.slideshowIncludeVideos === "yes",
                });
                setSettingsLoaded(true);
            }
        } catch (err) {
            console.error("Failed to load favorites:", err);
        } finally {
            setLoading(false);
        }
    }, []);

    // Handle interval change
    const handleIntervalChange = (changes: any) => {
        const newInterval = parseInt(changes.value, 10);
        setSlideshowSettings(prev => ({ ...prev, interval: newInterval }));
        updateSetting("slideshowInterval", String(newInterval)).catch(() => {
            toast.error("Failed to save interval setting");
        });
    };

    // Handle include videos toggle
    const handleIncludeVideosChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const newValue = event.target.checked;
        setSlideshowSettings(prev => ({ ...prev, includeVideos: newValue }));
        updateSetting("slideshowIncludeVideos", newValue ? "yes" : "no").catch(() => {
            toast.error("Failed to save video setting");
        });
    };

    useEffect(() => {
        loadData();

        // Listen for other tickers starting
        const unsubscribe = onTickerStarted((tickerType) => {
            if (tickerType !== 'slideshow') {
                setIsSlideshowRunning(false);
            }
        });

        // Poll status while slideshow is running
        const interval = setInterval(async () => {
            try {
                const status = await getSlideshowStatus();
                setIsSlideshowRunning(status.running || false);
                setCurrentMarquee(status.currentMarquee || "");
            } catch {
                // Ignore polling errors
            }
        }, 2000);

        return () => {
            unsubscribe();
            clearInterval(interval);
        };
    }, [loadData]);

    // Remove a favorite
    const handleRemoveFavorite = async (favorite: FavoriteItem) => {
        try {
            await removeSlideshowFavorite(favorite.key);
            setFavorites(prev => prev.filter(f => f.key !== favorite.key));
            toast.success("Removed from favorites");
        } catch {
            toast.error("Failed to remove favorite");
        }
    };

    // Start slideshow
    const handleStartSlideshow = async () => {
        if (favorites.length === 0) {
            toast.error("No favorites to display");
            return;
        }

        setIsStarting(true);
        try {
            const result = await startSlideshow(
                slideshowSettings.interval,
                slideshowSettings.includeVideos ? "yes" : "no"
            );

            if (result.status === "error") {
                toast.error(result.message || "Failed to start slideshow");
                setIsStarting(false);
                return;
            }

            setIsSlideshowRunning(true);
            dispatchTickerStarted('slideshow');
            toast.success("Slideshow Started!");
        } catch {
            toast.error("Failed to start slideshow");
        } finally {
            setIsStarting(false);
        }
    };

    // Stop slideshow
    const handleStopSlideshow = () => {
        toast.promise(stopSlideshow(), {
            loading: "Stopping...",
            success: () => {
                setIsSlideshowRunning(false);
                setCurrentMarquee("");
                return "Slideshow Stopped";
            },
            error: "Failed to stop slideshow",
        });
    };

    // Display a specific marquee
    const handleDisplayMarquee = (favorite: FavoriteItem) => {
        toast.promise(
            updateMarquee(favorite.system, favorite.game, true),
            {
                loading: "Displaying...",
                success: "Marquee displayed",
                error: "Failed to display marquee",
            },
            { id: "marquee-update" }
        );
    };

    if (loading) {
        return (
            <div className="favorites-page fade-in">
                <div className="favorites-loading">
                    <CircularProgress style={{ color: "#fff" }} />
                    <p>Loading favorites...</p>
                </div>
            </div>
        );
    }

    return (
        <div className="favorites-page fade-in">
            {/* Header */}
            <div className="favorites-header">
                <h2>
                    <Favorite style={{ marginRight: 8, color: "#ef4444", verticalAlign: "middle" }} />
                    Favorites ({favorites.length})
                </h2>
            </div>

            {/* Slideshow Controls */}
            <div className="slideshow-controls">
                <Button
                    variant="contained"
                    onClick={isSlideshowRunning ? handleStopSlideshow : handleStartSlideshow}
                    disabled={isStarting || (favorites.length === 0 && !isSlideshowRunning)}
                    className={`slideshow-button ${isSlideshowRunning ? "stop" : "start"}`}
                >
                    {isStarting ? (
                        <CircularProgress size={20} style={{ color: "#fff", marginRight: 8 }} />
                    ) : isSlideshowRunning ? (
                        <Stop style={{ marginRight: 8 }} />
                    ) : (
                        <PlayArrow style={{ marginRight: 8 }} />
                    )}
                    {isStarting ? "Starting..." : isSlideshowRunning ? "Stop Slideshow" : "Start Slideshow"}
                </Button>

                {settingsLoaded && (
                    <div className="slideshow-settings">
                        <div className="setting-item">
                            <label>Interval:</label>
                            <Select
                                name="interval"
                                options={intervalOptions}
                                selectedValue={String(slideshowSettings.interval)}
                                onChange={handleIntervalChange}
                                caretIcon={<CaretIcon />}
                                modalCloseButton={<ModalCloseButton />}
                            />
                        </div>
                        <FormControlLabel
                            control={
                                <Switch
                                    checked={slideshowSettings.includeVideos}
                                    onChange={handleIncludeVideosChange}
                                    color="primary"
                                />
                            }
                            label="Include Videos"
                            className="videos-toggle"
                        />
                    </div>
                )}

                {isSlideshowRunning && currentMarquee && (
                    <div className="now-playing">
                        Now showing: <strong>{currentMarquee}</strong>
                    </div>
                )}
            </div>

            {/* Favorites Grid */}
            {favorites.length === 0 ? (
                <div className="no-favorites">
                    <Favorite style={{ fontSize: 64, color: "#444", marginBottom: 16 }} />
                    <p>No favorites yet</p>
                    <p className="hint">
                        Browse marquees and tap the heart icon to add favorites
                    </p>
                    <Button
                        variant="contained"
                        onClick={() => nav("/consoles")}
                        style={{ marginTop: 16 }}
                    >
                        Browse Marquees
                    </Button>
                </div>
            ) : (
                <div className="favorites-grid">
                    {favorites.map((favorite) => (
                        <div
                            key={favorite.key}
                            className={`favorite-item ${currentMarquee === favorite.key ? "active" : ""}`}
                        >
                            <img
                                src={`${getDeviceUrl("")}/thumbnail/${favorite.system}/${favorite.game}.jpg`}
                                alt={favorite.game}
                                onClick={() => handleDisplayMarquee(favorite)}
                                onError={(e) => {
                                    const img = e.target as HTMLImageElement;
                                    const baseUrl = getDeviceUrl("");
                                    // Fallback order: game.jpg -> game.png -> default-system.jpg
                                    if (img.src.includes(`/${favorite.system}/${favorite.game}.jpg`)) {
                                        img.src = `${baseUrl}/thumbnail/${favorite.system}/${favorite.game}.png`;
                                    } else if (img.src.includes(`/${favorite.system}/${favorite.game}.png`)) {
                                        // Try game name without system prefix
                                        img.src = `${baseUrl}/thumbnail/${favorite.game}.jpg`;
                                    } else if (img.src.includes(`/thumbnail/${favorite.game}.jpg`)) {
                                        // Fall back to default system marquee
                                        img.src = `${baseUrl}/thumbnail/default-${favorite.system}.jpg`;
                                    }
                                }}
                            />
                            <div className="favorite-info">
                                <span className="game-name" title={favorite.game}>
                                    {favorite.game}
                                </span>
                                <span className="system-name">{favorite.system}</span>
                            </div>
                            <button
                                className="remove-button"
                                onClick={(e) => {
                                    e.stopPropagation();
                                    handleRemoveFavorite(favorite);
                                }}
                                title="Remove from favorites"
                            >
                                <Close fontSize="small" />
                            </button>
                            {favorite.hasVideo && (
                                <div className="video-indicator" title="Has video marquee">
                                    <Videocam fontSize="small" />
                                </div>
                            )}
                            {currentMarquee === favorite.key && (
                                <div className="playing-indicator">
                                    <PlayArrow fontSize="small" />
                                </div>
                            )}
                        </div>
                    ))}
                </div>
            )}
        </div>
    );
};

export default Favorites;
