import axios from "axios";
import { DeviceInfo } from "../types/DeviceInfo.type";

export const getDeviceUrl = (version: string = "/v2"): string => {
    return `http://${window.location.host}${version}`;
};

export const rebootSystem = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/reboot`;

    return axios.post(requestUrl).then((value) => {
        return value.data;
    });
};

export const shutdownSystem = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/shutdown`;

    return axios.post(requestUrl).then((value) => {
        return value.data;
    });
};

export const readDeviceInfo = (): Promise<DeviceInfo> => {
    const requestUrl = `${getDeviceUrl()}/info`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

export const readConsoles = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/info/consoles`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

export const readConsoleMarquees = (console: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/marquees/${console}`;
    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

