export const updateMarquee = (
    console: string,
    marquee: string,
    stream: boolean,
    noFuzzy: boolean = false
) => {
    const params = noFuzzy ? '?nofuzzy=1' : '';
    const requestUrl = `${getDeviceUrl("")}/arcade/stream/${console}/${marquee}${params}`;

    return axios.post(requestUrl);
};

export const updateDefaultMarquee = (id: string | number) => {
    const requestUrl = `${getDeviceUrl()}/utility/marquee/set/${id}`;
    return axios.get(requestUrl);
};

export const setScrollingText = (text: string) => {
    const requestUrl = `${getDeviceUrl("")}/text?t=${text}`;
    return axios.get(requestUrl);
};

export const readFilesystemMarquees = (console: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/filesystem/${console}`;
    return fetch(requestUrl).then((response) => response.json());
};

export const checkForUpdates = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/check-update`;
    return axios.get(requestUrl).then((value) => value.data);
};

// Check if the device has internet connectivity
export const getInternetStatus = (): Promise<{ hasInternet: boolean }> => {
    const requestUrl = `${getDeviceUrl()}/wifi/status`;
    return axios.get(requestUrl)
        .then((value) => ({ hasInternet: value.data?.hasInternet || false }))
        .catch(() => ({ hasInternet: false }));
};

// Refresh the marquee lookup cache (after adding new files)
export const refreshCache = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/cache/refresh`;
    return axios.post(requestUrl).then((value) => value.data);
};

// Slideshow API functions
export const getSlideshowStatus = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/status`;
    return axios.get(requestUrl).then((value) => value.data);
};

export const startSlideshow = (interval?: number, includeVideos?: string): Promise<any> => {
    let params = '';
    if (interval) params += `?interval=${interval}`;
    if (includeVideos) params += `${params ? '&' : '?'}includeVideos=${includeVideos}`;
    const requestUrl = `${getDeviceUrl("")}/slideshow/start${params}`;
    return axios.get(requestUrl).then((value) => value.data);
};

export const stopSlideshow = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/stop`;
    return axios.get(requestUrl).then((value) => value.data);
};

export const getSlideshowFavorites = (): Promise<string[]> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/favorites`;
    return axios.get(requestUrl).then((value) => value.data?.favorites || []);
};

export const addSlideshowFavorite = (marquee: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/favorite/add?marquee=${encodeURIComponent(marquee)}`;
    return axios.get(requestUrl).then((value) => value.data);
};

export const removeSlideshowFavorite = (marquee: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/favorite/remove?marquee=${encodeURIComponent(marquee)}`;
    return axios.get(requestUrl).then((value) => value.data);
};

export const isSlideshowFavorite = (marquee: string): Promise<boolean> => {
    const requestUrl = `${getDeviceUrl("")}/slideshow/isfavorite?marquee=${encodeURIComponent(marquee)}`;
    return axios.get(requestUrl).then((value) => value.data?.isFavorite || false);
};
